<template>
  <div>
    <div class="box-style pledge-box">
      <div class="gradient-label-box">
        <label class="gradient-label">LP {{ $t('hycFluidity.pledge') }}-{{ props.pledgeInfo.period }} {{
                $t('hycFluidity.day')
                }}</label>
      </div>
      <div class="pledge-info-box">
        <div class="my-pledge">
          <label class="pledge-title">{{ $t('hycFluidity.myPledge') }}</label>
          <label class="pledge-info">{{ props.pledgeInfo.principal }} LP</label>
        </div>
        <div class="my-pledge">
          <label class="pledge-title">{{ $t('hycFluidity.pledgeCycle') }}</label>
          <label class="pledge-info">{{ props.pledgeInfo.period }} {{ $t('hycFluidity.day') }}</label>
        </div>
        <div class="my-pledge">
          <label class="pledge-title">{{ $t('hycFluidity.myIncome') }}</label>
          <label class="pledge-info">{{ props.pledgeInfo.profit }} HYC</label>
        </div>
      </div>
      <div class="count-down">
        <van-count-down :time="countDownTime"
                        ref="countDown"
                        @finish="countDownFinish">
          <template #default="timeData">
            <div class="count-down-info">
              {{ timeData.days }}{{ $t('hycFluidity.day') }} {{ timeData.hours }}:{{ timeData.minutes
                            }}:{{ timeData.seconds }}
            </div>
          </template>
        </van-count-down>
      </div>
      <div class="button-box">
        <div class="button-style button-font"
             :style="(countDownTime > 0) ? ('opacity: 0.5;') : ('opacity: 1;')"
             @click="clickQuit">
          {{ $t('hycFluidity.quit') }}
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onUpdated, defineEmits } from 'vue';

const props = defineProps({
  pledgeInfo: {}
})
const emit = defineEmits(['quitPledge'])

onUpdated(() => {
  const nowTime = Math.round(new Date() / 1000)
  const totalTime = props.pledgeInfo.period * 60
  if (totalTime - (nowTime - props.pledgeInfo.start_time) > 0) {
    countDownTime.value = (totalTime - (nowTime - props.pledgeInfo.start_time)) * 1000
    countDown.value.start()
  } else {
    countDownTime.value = 0
    countDown.value.pause()
  }
})

const countDownTime = ref(0)
const countDown = ref(null)

const clickQuit = (() => {
  emit('quitPledge')
})

const countDownFinish = (() => {
  countDownTime.value = 0
  countDown.value.pause()
})

</script>

