<template>
  <div class="box-style mining-box">
    <div class="gradient-label-box">
      <label class="gradient-label">HYC {{ $t('home.fluidity') }}</label>
    </div>
    <div class="total-pledge">
      <label class="pledge-title">{{ $t('hycFluidity.totalPledge') }}</label>
      <label class="pledge-info">{{ state.totalPledge.toFixed(4) }} LP</label>
    </div>
    <div class="my-pledge">
      <label class="pledge-title">{{ $t('hycFluidity.myPledge') }}</label>
      <label class="pledge-info">{{ state.myPledge.toFixed(4) }} LP</label>
    </div>
    <div class="input-box">
      <van-field v-model="state.pledgeNumber"
                 :placeholder="$t('hycFluidity.inputPlaceholder')">
        <template #button>
          <div class="gradient-label"
               @click="clickPledgeAll">
            {{ $t('hycFluidity.all') }}
          </div>
        </template>
      </van-field>
    </div>
    <div class="pledge-title pledge-cycle-title">{{ $t('hycFluidity.pledgeCycle') }}</div>
    <van-row gutter="10">
      <van-col span="8"
               v-for="item in state.pledgeCycleList"
               :key="item">
        <div class="pledge-cycle"
             :style="(state.pledgeCycle == item) ? state.pledgeCycleSelectStyle : state.pledgeCycleUnSelectStyle"
             @click="clickPledgeCycle(item)">
          {{ item }} {{ $t('hycFluidity.day') }}
        </div>
      </van-col>
    </van-row>
    <div class="available-balance">
      <label class="pledge-title">{{ $t('hycFluidity.availableBalance') }}</label>
      <label class="pledge-info">{{ state.lpBalance.toFixed(4) }} LP</label>
    </div>
    <div class="button-box">
      <div class="button-style button-font"
           :style="(state.pledgeCycle == 0) ? ('opacity: 0.5;') : ('opacity: 1;')"
           @click="clickPledge">
        {{ $t('hycFluidity.pledge') }}
      </div>
    </div>
    <div class="available-balance">
      <label class="pledge-title">{{ $t('hycFluidity.withdrawableBalance') }}</label>
      <label class="pledge-info">{{ state.userInfo.hyc ?? 0 }} HYC</label>
    </div>
    <div class="button-box">
      <div class="button-style button-font"
           :style="(state.userInfo.hyc > 0) ? ('opacity: 1;') : ('opacity: 0.5;')"
           @click="clickExtract">
        {{ $t('hycFluidity.extract') }}
      </div>
    </div>
  </div>
  <HYCPledge v-for="item in state.pledgeList"
             :key="item.id"
             :pledgeInfo="item"
             @quitPledge="quitPledge(item)">
  </HYCPledge>
</template>

<script setup>

import { reactive, ref, onMounted, onUnmounted, getCurrentInstance, watch } from 'vue';
import { useRoute } from 'vue-router';
import staking from '@/json/SADHYCStaking.json'
import token from '@/json/token.json'
import { ethers, utils, constants } from 'ethers';
import { getUser, getPledgeOrder, extract } from '@/utils/api'
import HYCPledge from '@/components/HYCPledge.vue';
import { Toast } from 'vant';
import i18n from '@/i18n';

const { proxy } = getCurrentInstance()
const route = useRoute()
const timer = ref(0)

const state = reactive({
  pledgeCycleList: [],
  pledgeCycle: 0,
  pledgeCycleUnSelectStyle: {
    border: '1px solid #373B44'
  },
  pledgeCycleSelectStyle: {
    border: '1px solid #9EE4FF'
  },

  userInfo: {},
  pledgeNumber: 0,

  stakingContractAddress: process.env.VUE_APP_SAD_HYC_STAKING_CONTRACT_ADDRESS,
  stakingContract: null,
  lpContractAddress: process.env.VUE_APP_SAD_HYC_CONTRACT_ADDRESS,
  lpContract: null,
  
  totalPledge: 0,
  myPledge: 0,
  lpBalance: 0,
  pledgeList: []
})

watch(() => route.account, () => {
  checkAuthorizeStatus()
  getUserInfo()
  getPledgeInfo()
})
onMounted(async () => {
  state.stakingContract = new ethers.Contract(state.stakingContractAddress, staking, proxy.$provider.getSigner())
  state.lpContract = new ethers.Contract(state.lpContractAddress, token, proxy.$provider.getSigner())

  setIntervalRefreshData()
  checkAuthorizeStatus()
  getPledgeCycleInfo()
  getTotalSupply()
  getUserInfo()
  getPledgeInfo()
})

const setIntervalRefreshData = (() => {
  timer.value = window.setInterval(async () => {
    getTotalSupply()
    getUserInfo()
    getPledgeInfo()
  }, 3000)
})
const clearIntervalRefreshData = (() => {
  window.clearInterval(timer.value)
})

onUnmounted(() => {
  clearIntervalRefreshData()
})

const getPledgeCycleInfo = (async () => {
  const periodCount = await state.stakingContract.periodCount()
  for (let index = 0; index < periodCount; index++) {
    const periods = await state.stakingContract.periods(index)
    state.pledgeCycleList.push(periods)
  }
  state.pledgeCycleList.sort((a, b) => a - b)
})
const quitPledge = (async (item) => {
  Toast.loading()
  const exit = await state.stakingContract.exit(item.period)
  exit.wait()
  getPledgeInfo()
})

const getTotalSupply = (async () => {
  const totalSupply = await state.stakingContract.totalSupply()
  state.totalPledge = parseFloat(utils.formatEther(totalSupply))
})
const getUserInfo = (async () => {
  const balanceOf = await state.stakingContract.balanceOf(route.account)
  state.myPledge = parseFloat(utils.formatEther(balanceOf))

  const lpBalanceOf = await state.lpContract.balanceOf(route.account)
  state.lpBalance = parseFloat(utils.formatEther(lpBalanceOf))

  const { data } = await getUser({
    address: route.account
  })
  state.userInfo = data
})
const getPledgeInfo = (async () => {
  if (route.account) {
    const { data } = await getPledgeOrder({
      address: route.account
    })
    state.pledgeList = data
  }
})

const checkAuthorizeStatus = (async () => {
  const allowance = await state.lpContract.allowance(route.account, state.stakingContractAddress)
  if (allowance.eq(constants.MaxUint256)) {
    route.isSADHYCAuthorize = true
  } else {
    route.isSADHYCAuthorize = false
  }
})

const clickPledgeCycle = ((cycle) => {
  state.pledgeCycle = cycle
})
const clickPledgeAll = (() => {
  state.pledgeNumber = state.lpBalance
})

const clickPledge = (() => {
  for (const pledgeInfo of state.pledgeList) {
    if (pledgeInfo.period == state.pledgeCycle) {
      Toast.fail(i18n.global.t('toast.alreadyPledge'))
      return
    }
  }
  if (state.pledgeNumber > state.lpBalance) {
    Toast.fail(i18n.global.t('toast.insufficientBalance'))
  } else if (parseFloat(state.pledgeNumber) >= 1) {
    startPledge()
  } else {
    Toast.fail(i18n.global.t('toast.inputError'))
  }
})

const startPledge = (async () => {
  if (route.isSADHYCAuthorize) {
    Toast.loading()
    const stake = await state.stakingContract.stake(utils.parseEther(state.pledgeNumber.toString()), state.pledgeCycle)
    stake.wait()
    state.pledgeCycle = 0
    state.pledgeNumber = 0
    getTotalSupply()
    getUserInfo()
  } else {
    Toast.loading()
    const approve = await state.lpContract.approve(state.stakingContractAddress, constants.MaxUint256)
    approve.wait()
    route.isSADHYCAuthorize = true
    startPledge()
  }
})

const clickExtract = (async () => {
  if (state.userInfo.hyc > 0) {
    Toast.loading()
    const { data } = await extract({
      address: route.account
    })
    Toast.loading()
    const withdraw = await state.stakingContract.withdraw(data.address, data.amount, data.deadline, data.sign)
    withdraw.wait()
    getUserInfo()
  }
})

</script>

